body {
  margin: 0;
  padding: 1rem;
  font-family: sans-serif;
  background: #eee;
  color: #222;
}

.searchBox {
  min-width: 500px;
  padding: 10px;
  font-size: 1.8rem;
}

.schType {
  margin-top: 1rem;
}

ul {
  margin: 0 0 0 2rem;
  padding: 0;
  display: inline-block;
}

li {
  display: inline-block;
  min-width: 50px;
  text-align: center;
  cursor: pointer;
}

li.sel {
  text-decoration: overline;
  font-weight: bold;
  pointer-events: none;
}

.selOpt {
  font-size: 1.2rem;
}

.frmBtn {
  margin-left: 1rem;
  font-size: 1.2rem;
  padding: 1rem;
  width: 100px;
}

.entity {
  background-color: #333;
  color: #EEE;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 0.8rem;
  margin-bottom: 0.3rem;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
}

.searchResults {
  margin-top: 40px;
}

.searchResults > span {
  font-size: 1rem;
  color: #888;
}

.entity .main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.entity .title {
  font-size: 1.2rem;
  font-weight: bold;
}

.entity .info {
  font-size: 0.8rem;
  color: #888;
}

.entity .hover {
  display: none;
  -ms-flex-item-align: center;
      align-self: center;
}

.entity:hover .hover {
  display: block;
}

.entity .loading {
  padding: 0.5rem;
  -ms-flex-item-align: center;
      align-self: center;
}

.onBack {
  background-color: #282828;
  border: 1px solid #444;
  border-radius: 5px;
  font-size: 1.2rem;
  padding: 0.2rem 0.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  color: #EEE;
  font-size: 1rem;
}

.spinner {
  text-align: center;
  -webkit-animation: spin infinite 2s linear reverse;
          animation: spin infinite 2s linear reverse;
  width: 35px;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



